    export default {
        name: "app-special-topic",
	    props: {
            topic_list: {
                type: Array,
				default() {
                    return [];
				}
			},
            count: {
                type: Number,
				default() {
                    return 2;
				}
			},
            icon: String,
            logo_1: String,
            logo_2: String,
	    },
	    computed: {
            newDataList: function() {
                if (this.count !== 2) return;
                let newTopicList = [];
                for (let i = 0; i < Math.ceil(this.topic_list.length/this.count); i++) {
                    newTopicList.push(this.topic_list.slice(i*this.count, (i+1)*this.count));
                }
                return newTopicList;
            }
	    }
    }
